@import url("https://fonts.googleapis.com/css2?family=Nunito&family=Red+Hat+Mono:wght@500&family=Roboto+Condensed&family=Roboto+Mono:ital,wght@1,200&display=swap");
body {
    margin: 0;
    padding: 0;
    font-family: "Nunito", sans-serif;
    font-family: "Red Hat Mono", monospace;
    font-family: "Roboto Condensed", sans-serif;
    font-family: "Roboto Mono", monospace;
    background-color: rgb(250, 250, 250);
}

nav ul {
    list-style-type: none;
    display: flex;
    padding: 2em;
    padding-left: 3em;
    margin: 0;
    box-shadow: 0 0.25em 0.25em 0 rgba(0, 0, 0, 0.25);
    background-color: white;
}

nav li {
    margin-right: 1em;
}

nav li a {
    text-decoration: none;
    color: rgb(74, 74, 74);
    font-size: 1.5em;
    color: #18abf4;
}

nav li a:hover {
    color: #4504ea;
}

.icon {
    color: #4504ea;
}

.icon:hover {
    opacity: 0.5;
}

h1,
h2,
h3,
h4,
h5 {
    margin: 0;
}

.title {
    text-align: center;
    font-size: 2.5em;
    font-weight: 900;
    margin: 0.5em;
    color: #18abf4;
}

.footer {
    margin-top: 1rem;
    padding: 1rem;
    background-color: white;
    box-shadow: 0 0 0.15em 0.15em rgba(0, 0, 0, 0.25);
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    text-align: center;
}

#footer-link {
    color: #18abf4;
    text-decoration: none;
    display: block;
    display: flex;
    justify-content: center;
}

#footer-link:hover {
    opacity: 0.5;
}

#footer-link a {
    text-decoration: none;
}

.devs {
    color: #4504ea;
}

.page {
    background-color: white;
    width: 90vw;
    margin: 0 auto;
    padding-bottom: 2em;
    margin-bottom: 5em;
    box-shadow: 0.25em 0.25em 0.25em 0.25em rgba(0, 0, 0, 0.25);
}

.paragraph {
    text-indent: 2em;
    padding-right: 3em;
    padding-left: 3em;
    margin: 0;
}

.contact-card {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: fit-content;
}

.contact-card a {
    color: #18abf4;
}

.contact-card a:hover {
    color: #4504ea;
}

form {
    height: 22rem;
}

form input,
textarea {
    padding: 1em;
    margin: 0.25em;
    border-radius: 1em;
    border: solid #4504ea;
    color: #4504ea;
}

::placeholder {
    color: #18abf4;
}

form button {
    padding: 0.5rem;
    width: 10rem;
    font-size: 2em;
    margin: 0.5rem;
    outline: none;
    border: none;
    background: #4504ea;
    color: white;
    box-shadow: 0 0.25em 0.25em 0 rgba(0, 0, 0, 0.25);
    border-radius: 1em;
    cursor: pointer;
}

form button:hover {
    background: #3d0db5;
    padding: 0.4rem;
    width: 9rem;
    font-size: 1.75em;
}

@media only screen and (max-width: 400px) {
    nav ul {
        display: block;
    }
}